import Vue from 'vue'
import VueRouter from 'vue-router'
/*
import Home from '../pages/home'
*/
//import Detail from '../pages/goodsDetail'
//打包时使用异步promise打包，免得包太大
const Detail = r => require.ensure([], () => r(require('../pages/goodsDetail.vue')))
//import Msg from '../components/Message.vue'
const Msg = r => require.ensure([], () => r(require('../components/Message.vue')))
//import Login from '../pages/loginPage'
const Login = r => require.ensure([], () => r(require('../pages/loginPage.vue')))
//import About from '../pages/about'
const About = r => require.ensure([], () => r(require('../pages/about.vue')))
//import Call from '../pages/call'
const Call = r => require.ensure([], () => r(require('../pages/call.vue')))


// 这两条路由被打包在相同的块中，访问任一路由都会延迟加载该路由组件
//例如我想一进个人中心就加载里面所有的模块和路由
//const OtherMassivePage = r => require.ensure([], () => r(require('./routes/OtherMassivePage.vue')), 'big-pages')
//const WeightLossPage = r => require.ensure([], () => r(require('./routes/WeightLossPage.vue')), 'big-pages')



Vue.use(VueRouter)
const routes = [
  {
    path: '/index',
    component: resolve => require(['../pages/home'],resolve),  //异步获取组件
    meta: {
      title:'home' //页面标题
    },
    //在这个路由里触发
//  beforeEnter: function(to,from,next){
//      console.log(to)
//      console.log(from)
//  }
  },
  {
    path: '/msg',
    component: Msg
  },
  {
    path: '/detail',
    component: Detail,
    children: [
      {
          path:'msg',
          component: Msg
      }
    ]
  },
	{
		path: '/',
		component: Login,
	},
	{
		path: '/about',
		component: About,
	},
	{
		path: '/call',
		component: Call,
	},
]
export default new VueRouter({
  routes
})



